import React, { Component } from "react";
import { connect } from "react-redux";
import { getProgrammesResults } from "../../action/registrationActions";
import LoadingSpinner from "../../components/LoadingSpinner";
import PeriodResults from "../../components/academic/PeriodResults";
import { ExclamationCircle } from "react-bootstrap-icons";
import GeneralError from "../../components/errors/GeneralError";

class Transcript extends Component {
  componentDidMount() {
    const { programme_session_id, is_old } = this.props.match.params;
    this.props.getProgrammesResults(programme_session_id, is_old);
  }

  render() {
    const { transcript } = this.props;
    return (
      <div class="col-lg-12 my-2">
        <div class="card shadow-sm">
          <div class="card-header fw-bold">Online Transcript</div>
          <div class="card-body">
            <table class="table table-bordered table-striped">
              {transcript.status != undefined ? (
                transcript.status === true ? (
                  Object.entries(transcript.data.results).map((key, value) => {
                    return (
                      <PeriodResults results={transcript.data.results[value]} />
                    );
                  })
                ) : (
                  <GeneralError message={transcript.message}/>
                )
              ) : (
                <LoadingSpinner />
              )}
            </table>
          </div>
        </div>
      </div>
    );
  }
}

const mapStateToProps = (state) => ({
  transcript: state.registration.transcript,
});

export default connect(mapStateToProps, { getProgrammesResults })(Transcript);
