import store from "../store";

export const baseUrl = "http://elearning.msu.ac.zw:8000/https://elearning.msu.ac.zw";
export const apiBaseUrl = `${baseUrl}/data`;

export const getAuthSessionId = () => {
  //const { auth } = store.getState();
  return localStorage.getItem("mymsu_session_id");
};

export const getDefaultAPIHeaders = () => {
  const headers = {
    "Content-Type": "application/json",
    sessionid: getAuthSessionId(),
  };
  return headers;
};
