import { LOGIN_USER } from "../action/types";

const initialState = {};

export default function (state = initialState, action) {
  switch (action.type) {
    case LOGIN_USER:
      if (action.payload.sessionid == undefined) {
        return { ...state, ...action.payload };
      }
      localStorage.setItem("mymsu_session_id", action.payload.sessionid);
      window.location.href = "/";

      return { ...state, ...action.payload };
    default:
      return state;
  }
}
