import React, { Component } from "react";
import NumberFormat from "react-number-format";

export default class StatementTransaction extends Component {
  render() {
    const { reference, date, description, amount, debit, credit } = this.props;
    return (
      <React.Fragment>
        <div
          class="list-group-item list-group-item-action flex-column align-items-start"
        >
          <div class="d-flex w-100 justify-content-between">
            <p class="mb-1 fw-bold">REFERENCE: {reference}</p>
        
            {credit > 0 ? (
              <small class="fw-bold h6 text-success">
                <NumberFormat
                  class="h5"
                  value={credit}
                  displayType={"text"}
                  thousandSeparator={true}
                  prefix={"$"}
                  decimalScale={2}
                  fixedDecimalScale={true}
                />
              </small>
            ) : (
              <small class="fw-bold h6 text-danger">
                <NumberFormat
                  class="h5"
                  value={debit}
                  displayType={"text"}
                  thousandSeparator={true}
                  prefix={"$"}
                  decimalScale={2}
                  fixedDecimalScale={true}
                />
              </small>
            )}
          </div>
          <p class="mb-1">{description} </p>
        </div>
      </React.Fragment>
    );
  }
}
