import React, { Component } from "react";
import { connect } from "react-redux";
import { getBalances } from "../../action/financeActions";
import LoadingSpinner from "../LoadingSpinner";
import DummyCardLink from "../DummyCardLink";
import NumberFormat from "react-number-format";

import { Link } from "react-router-dom";


class Balance extends Component {
  componentDidMount() {
    this.props.getBalances();
  }
  render() {
    const { balances } = this.props;
    return (
      <div class="col-lg-4 col-sm-12 my-2">
        <div class="card text-center shadow-sm">
          <div class="card-header fw-bold">Fees Balances</div>
          <div class="card-body">
            {isNaN(balances.fees.zwl) == false ? (
              <div class="row">
                <div class="col-lg-6">
                  <p class="fw-bold">
                    <NumberFormat
                      class="h5"
                      value={balances.fees.zwl}
                      displayType={"text"}
                      thousandSeparator={true}
                      prefix={"$"}
                      decimalScale={2}
                      fixedDecimalScale={true}
                    />
                    <span class="small">ZWL</span>
                  </p>
                </div>
                <div class="col-lg-6">
                  <p class="fw-bold">
                    <NumberFormat
                      class="h5"
                      value={balances.fees.usd}
                      displayType={"text"}
                      thousandSeparator={true}
                      prefix={"$"}
                      decimalScale={2}
                      fixedDecimalScale={true}
                    />
                    <span class="small">USD</span>
                  </p>
                </div>
              </div>
            ) : (
              <LoadingSpinner />
            )}
          </div>

          <div class="card-footer text-center bg-white">
            {isNaN(balances.fees.zwl) == false ? (
              <React.Fragment>
                <Link to="/finance/payment" class="card-link">
                  Make Payment
                </Link>
                <Link to="/finance/statement/fees" class="card-link">
                  Statement
                </Link>
              </React.Fragment>
            ) : (
              <DummyCardLink />
            )}
          </div>
        </div>
      </div>
    );
  }
}

const mapStateToProps = (state) => ({
  balances: state.finance.balances,
});

export default connect(mapStateToProps, { getBalances })(Balance);
