import React, { Component } from "react";
import { connect } from "react-redux";
import PropTypes from "prop-types";
import { getRegistrationRecord } from "../../action/registrationActions";
import LoadingSpinner from "../../components/LoadingSpinner";

class RegistrationRecord extends Component {
  componentDidMount() {
    this.props.getRegistrationRecord();
  }
  render() {
    const { academicyear, semester, period, modules, status } =
      this.props.record.record;
    console.log("RECORD");
    console.log(modules);

    return (
      <React.Fragment>
        <div class="card col-lg-12 m-1">
          <div class="card-body">
            {modules !== undefined ? (
              <React.Fragment>
                <p class="text-uppercase">
                  <b>Period:</b> {period}
                </p>
                <p class="text-uppercase">
                  <b>Academic Year:</b> {academicyear}
                </p>
                <p class="text-uppercase">
                  <b>Semester:</b> {semester}
                </p>
                <table class="table table-bordered table-striped">
                  <thead>
                    <tr>
                      <th>MODULE CODE</th>
                      <th>MODULE NAME</th>
                      <th>CREDITS</th>
                    </tr>
                  </thead>
                  <tbody>
                    {modules != undefined ? (
                      Object.entries(modules).map((key, value) => {
                        console.log(modules[value]);
                        return (
                          <tr
                            class="fw-normal"
                            key={modules[value]["module_id"]}
                          >
                            <td>{modules[value]["module_code"]}</td>
                            <td>{modules[value]["module_name"]}</td>
                            <td>
                              <span class="badge bg-dark text-white">
                                {modules[value]["credit"]}
                              </span>
                            </td>
                          </tr>
                        );
                      })
                    ) : (
                      <React.Fragment />
                    )}
                  </tbody>
                </table>
              </React.Fragment>
            ) : (
              <LoadingSpinner />
            )}
          </div>
        </div>
      </React.Fragment>
    );
  }
}

const mapStateToProps = (state) => ({
  record: state.registration,
});

export default connect(mapStateToProps, { getRegistrationRecord })(
  RegistrationRecord
);
