import React, { Component } from "react";
import Balance from "../../components/finance/Balance";
import Statement from "../../components/finance/Statement";

export default class NostroStatement extends Component {
    render() {
        return (
            <div class="mx-auto">
                <header className="App-header"></header>
                <div class="col-lg-12 row p-3">
                    <Balance />
                    <Statement
                        title="USD FEES STATEMENT"
                        statement_type="fees"
                        currency="usd"
                    />
                </div>
            </div>
        );
    }
}
