import { GET_PROGRAMMES, GET_PROGRAMME_RESULTS, GET_REGISTRATION_RECORD } from "../action/types";

const initialState = {
  status: undefined,
  programmes: undefined,
  transcript:{},
  record:{}
};

export default function (state = initialState, action) {
  switch (action.type) {
    case GET_REGISTRATION_RECORD:
      return { ...state, ...action.payload };

    case GET_PROGRAMMES:
      return { ...state, ...action.payload };

    case GET_PROGRAMME_RESULTS:
      return { ...state, ...action.payload };

    default:
      return state;
  }
}
