import React, {Component} from "react";
import {BrowserRouter as Router, Route, Switch} from "react-router-dom";
import store from "./store";
import Home from "./pages/Home";
import NotFound from "./pages/NotFound";
import Header from "./components/layout/Header";
import {Provider} from "react-redux";
import FeesStatement from "./pages/FeesStatement";
import Login from "./pages/auth/Login";
import Transcript from "./pages/academic/Transcript";

import "bootstrap/dist/css/bootstrap.min.css";
import { popper } from "@popperjs/core";
import "./App.css";
import Logout from "./components/Logout";
import RegistrationRecord from "./pages/academic/RegistrationRecord";
import {Offline} from "react-detect-offline";
import NostroStatement from "./pages/financial/NostroStatement";
import PersonalDetails from "./pages/PersonalDetails";

class App extends Component {
    render() {
        const {auth} = store.getState();
        console.log("AUTH STATE");
        return (
            <Provider store={store}>
                <Router>
                    <React.Fragment>
                        <Header branding="myMSU"/>
                            <div className="container p-0">
                                <Switch>
                                    <Route exact path="/" component={Home}/>
                                    <Route exact path="/academic/transcript/:programme_session_id/:is_old" component={Transcript}/>
                                    <Route exact path="/financial/statement/fees" component={FeesStatement}/>
                                    <Route exact path="/financial/statement/fees/nostro" component={NostroStatement}/>
                                    <Route exact path="/registration/record" component={RegistrationRecord}/>
                                    <Route exact path="/personal" component={PersonalDetails}/>
                                    <Route exact path="/login" component={Login}/>
                                    <Route exact path="/logout" component={Logout}/>
                                    <Route component={NotFound}/>
                                </Switch>
                            </div>
                    </React.Fragment>
                </Router>
            </Provider>
        );
    }
}

export default App;
