import React, { Component } from "react";
import { connect } from "react-redux";
import { getGoldCardBalance } from "../../action/financeActions";
import LoadingSpinner from "../LoadingSpinner";
import DummyCardLink from "../DummyCardLink";
import NumberFormat from "react-number-format";

class GoldCardBalance extends Component {
  componentDidMount() {
    this.props.getGoldCardBalance();
  }
  render() {
    const { goldcard_balance } = this.props.balances;
      console.log("GOLDCARD")
      console.log(goldcard_balance);
    return (
      <div class="col-lg-4 col-sm-12 my-2">
        <div class="card text-center shadow-sm">
          <div class="card-header fw-bold">Gold Card</div>
          <div class="card-body">
            {isNaN(goldcard_balance) == false ? (
              <p class="fw-bold">
                <NumberFormat
                  class="h3"
                  value={goldcard_balance}
                  displayType={"text"}
                  thousandSeparator={true}
                  decimalScale={2}
                  fixedDecimalScale={true}
                  prefix={"$"}
                />
                <span class="small">ZWL</span>
              </p>
            ) : (
              <LoadingSpinner />
            )}
          </div>
          <div class="card-footer text-center bg-white">
            {isNaN(goldcard_balance) == false ? (
              <React.Fragment>
                <a href="#" class="card-link">
                  Make Payment
                </a>
                <a href="#" class="card-link">
                  Statement
                </a>
              </React.Fragment>
            ) : (
              <DummyCardLink />
            )}
          </div>
        </div>
      </div>
    );
  }
}

const mapStateToProps = (state) => ({
  balances: state.finance,
});

export default connect(mapStateToProps, { getGoldCardBalance })(
  GoldCardBalance
);
