import {
  GET_BALANCES,
  GET_FEES_STATEMENT,
  GET_GOLDCARD_BALANCE,
} from "../action/types";

const initialState = {
  balances: {
    gold_card: NaN,
    fees: {
      zwl: NaN,
      usd: NaN,
    },
  },
  goldcard_balance: undefined,
  statements: {
    gold_card: {},
    fees: {
      zwl: [],
      usd: [],
    },
  },
};

export default function (state = initialState, action) {
  switch (action.type) {
    case GET_BALANCES:
      return {
        ...state,
        balances: {
          fees: action.payload,
        },
      };

    case GET_GOLDCARD_BALANCE:
      return {
        ...state,
        goldcard_balance: action.payload,
      };

    case GET_FEES_STATEMENT:
      return {
        ...state,
        statements: { fees: action.payload },
      };

    default:
      return state;
  }
}
