import { GET_BALANCES, GET_FEES_STATEMENT, GET_GOLDCARD_BALANCE } from "./types";
import { apiBaseUrl, getAuthSessionId } from "../services/Config";
import axios from "axios";

export const getBalances = () => async (dispatch) => {
  await axios
    .get(`${apiBaseUrl}/financial/fees_balance`, {
      headers: {
        "Content-Type": "application/json",
        sessionid: getAuthSessionId(),
      },
    })
    .then((response) => {
      dispatch({
        type: GET_BALANCES,
        payload: {
          zwl: response.data.zwl,
          usd: response.data.usd,
        },
      });
    })
    .catch((error) => {
      dispatch({
        type: GET_BALANCES,
        payload: {
          zwl: undefined,
          usd: undefined,
        },
      });
    });
};


