import React, { Component } from "react";
import { Link } from "react-router-dom";

export default class Programme extends Component {
  render() {
    const { programme } = this.props;

    return (
      <React.Fragment>
        <li class="list-group-item">
          <p class="font-weight-normal">
            <span class="fw-bold">
              {programme.programme}
              <Link
                class="btn btn-xs btn-outline-primary float-end"
                to={`/academic/transcript/${programme.programmesessionid}/${programme.is_old}`}
              >
                View Results
              </Link>
            </span>
          </p>
          <label class="badge bg-dark">{programme.attendace_type}</label>
          {programme.is_expired ? (
            <label class="badge bg-danger mx-1">Programme Lapsed</label>
          ) : (
            <React.Fragment />
          )}
          {(programme.campus_name.length>0) ? (
            <label class="badge bg-success mx-1">
              {programme.campus_name} Campus
            </label>
          ) : (
            <React.Fragment></React.Fragment>
          )}
        </li>
      </React.Fragment>
    );
  }
}
