import React, { Component } from "react";
import { connect } from "react-redux";
import PropTypes from "prop-types";
import { getRegistrationRecord } from "../../action/registrationActions";
import { Link } from "react-router-dom";
import LoadingSpinner from "../LoadingSpinner";
import DummyCardLink from "../DummyCardLink";

import NoInternet from "../../components/errors/NoInternet";

class RegistrationStatus extends Component {
  componentDidMount() {
    this.props.getRegistrationRecord();
  }
  render() {
    const { status } = this.props.record.record;
    let cardBackground = "#fff";
    let textColor = "#444";

    switch (status) {
      case true:
        cardBackground = "#28a745";
        textColor = "#fff";
        break;

      case false:
        cardBackground = "#dc3545";
        textColor = "#fff";
        break;
        
      case undefined:
        textColor = "#444";
        break;
    }

    return (
      <div class="col-lg-4 col-sm-12 my-2">
        <div
          style={{ backgroundColor: cardBackground, color: textColor }}
          class="card shadow-sm text-center"
        >
          <div class="card-header fw-bold">Registration Status</div>
          <div class="card-body">
            {status != undefined ? (
              <p class="fw-bold">
                <span class="h3">
                  {status ? "Registered" : "Not Registered"}
                </span>
              </p>
            ) : (
               <LoadingSpinner/>
            )}
          </div>
          <div class="card-footer text-center">
            {status != undefined ? (
              status ? (
                <Link to="/registration/record" class="card-link text-white">
                  Registration Record
                </Link>
              ) : (
                <React.Fragment>
                  <Link to="/registration/report" class="card-link text-white">
                    Report Issue
                  </Link>

                  <Link
                    to="/finance/statement/fees"
                    class="card-link text-white"
                  >
                    Fees Structure
                  </Link>
                </React.Fragment>
              )
            ) : (
              <DummyCardLink />
            )}
          </div>
        </div>
      </div>
    );
  }
}

const mapStateToProps = (state) => ({
  record: state.registration,
  toggleRegistrationStatus: state.registration.toggleRegistrationStatus,
});

export default connect(mapStateToProps, { getRegistrationRecord })(
  RegistrationStatus
);
