import React from 'react'
import { Book, Box, Calendar, FileBarGraph, Google, GraphDown, GraphUp, Table } from "react-bootstrap-icons";

export default function AcademicQuickLinks() {
    return (
      <React.Fragment>
        <div class="card h-100 animate-if" ng-if="get_fees_loaded">
          <div class="card-header fw-bold text-center">Academics</div>
          <div class="card-body">
            <div class="media">
              <div class="media-body">
                <a
                  href="#!assessment"
                  class="list-group-item list-group-item-action"
                >
                  <GraphUp/> Continuous Assessment
                </a>
                <a
                  href="#!timetable"
                  class="list-group-item list-group-item-action"
                >
                  <Table/> Exam Timetable
                </a>
                <a
                  href="#!timetable/teaching"
                  class="list-group-item list-group-item-action"
                >
                  <Calendar/> Teaching Timetable
                </a>
                <a
                  href="#!classes"
                  class="list-group-item list-group-item-action"
                >
                  <Google/> Online Classes
                </a>
                <a
                  href="#!library/menu"
                  class="list-group-item list-group-item-action"
                >
                  <Book/> Library
                </a>
              </div>
            </div>
          </div>
        </div>
      </React.Fragment>
    );
}
