import React, { Component } from "react";

export default class PeriodResults extends Component {
  render() {
    const { results } = this.props;

    console.log("TRANSCRIPT");
    console.log(results);
    return (
      <React.Fragment>
        <tr>
          <td class="fw-bold">ACADEMIC YEAR:</td>
          <td class="fw-bold">{results.academicyear}</td>
          <td></td>
          <td></td>
        </tr>
        <tr>
          <td class="fw-bold">SEMESTER:</td>
          <td class="fw-bold">{results.semester}</td>
          <td></td>
          <td></td>
        </tr>
        <tr>
          <td class="fw-bold">PERIOD:</td>
          <td class="fw-bold">{results.period}</td>
          <td></td>
          <td></td>
        </tr>
        <tr>
          <td>Module Code</td>
          <td>Module Name</td>
          <td>Class</td>
          <td>Decision</td>
        </tr>

        {Object.entries(results.data).map((key, value) => {
          //console.log(transcript.data.results[value]);
          return (
            <tr>
              <td>{results.data[value]["module"]}</td>
              <td>{results.data[value]["name"]}</td>
              <td>{results.data[value]["class"]}</td>
              <td>
                <span class="badge fw-bold bg-success">
                  {results.data[value]["decision"]}
                </span>
              </td>
            </tr>
          );
        })}
        <tr>
          <td class="fw-bold">DECISION:</td>
          <td class="fw-bold">{results.decision}</td>
          <td></td>
          <td></td>
        </tr>
        <tr class="py-5">
          <td></td>
          <td></td>
          <td></td>
          <td></td>
        </tr>
      </React.Fragment>
    );
  }
}
