import {
  GET_REGISTRATION_RECORD,
  GET_PROGRAMMES,
  GET_PROGRAMME_RESULTS,
} from "./types";
import { apiBaseUrl, getAuthSessionId } from "../services/Config";
import axios from "axios";

export const getRegistrationRecord = () => async (dispatch) => {
  const response = await axios.get(`${apiBaseUrl}/registration/record`, {
    headers: {
      "Content-Type": "application/json",
      sessionid: getAuthSessionId(),
    },
  });

  dispatch({
    type: GET_REGISTRATION_RECORD,
    payload: { record: response.data },
  });
};

export const getStudentProgrammes = () => async (dispatch) => {
  const response = await axios.get(`${apiBaseUrl}/student/programmes`, {
    headers: {
      "Content-Type": "application/json",
      sessionid: getAuthSessionId(),
    },
  });

  dispatch({
    type: GET_PROGRAMMES,
    payload: {
      programmes: response.data.data,
    },
  });
};

export const getProgrammesResults =
  (programme_session_id, is_old = 0) =>
  async (dispatch) => {
    const response = await axios
      .get(`${apiBaseUrl}/academic/results/${programme_session_id}/${is_old}`, {
        headers: {
          "Content-Type": "application/json",
          sessionid: getAuthSessionId(),
        },
      })
      dispatch({
        type: GET_PROGRAMME_RESULTS,
        payload: {
          transcript: response.data,
        },
      });
  };
