import React, { Component } from "react";
import { connect } from "react-redux";
import { getBalances, getFeesStatement } from "../../action/financeActions";
import StatementTransaction from "./StatementTransaction";
import LoadingSpinner from "../LoadingSpinner";
import NumberFormat from "react-number-format";
import BalanceLine from "./BalanceLine";

class Statement extends Component {
  componentDidMount() {
    const { currency } = this.props;
    this.props.getBalances();
    this.props.getFeesStatement(currency);
  }
  render() {
    const { title, statements, statement_type, currency } = this.props;

    var statement_source;
    switch (statement_type) {
      case "gold_card":
        if (currency == "zwl") {
          statement_source = statements.gold_card;
        }
        break;

      case "fees":
        if (currency == "zwl") statement_source = statements.zwl;
        if (currency == "usd") statement_source = statements.usd;

        break;

      default:
        break;
    }
    console.log(statement_source);
    return (
      <div class="col-lg-12 my-2">
        {statement_source != undefined ? (
          <BalanceLine amount={statement_source.balance} />
        ) : (
          <React.Fragment />
        )}
        <div class="card shadow-sm mb-2">
          <div class="card-header fw-bold">{title}</div>
          <div class="card-body">
            <div class="list-group list-group-flush">
              {statement_source.data != undefined ? (
                statement_source.data.map((transaction) => (
                  <StatementTransaction
                    date={transaction.txn_date}
                    reference={transaction.reference}
                    description={transaction.description}
                    credit={transaction.credit}
                    debit={transaction.debit}
                  />
                ))
              ) : (
                <LoadingSpinner />
              )}
            </div>
          </div>
        </div>
        {statement_source != undefined ? (
          <BalanceLine amount={statement_source.balance} />
        ) : (
          <React.Fragment />
        )}
      </div>
    );
  }
}

const mapStateToProps = (state) => ({
  balances: state.finance.balances,
  statements: state.finance.statements.fees,
});

export default connect(mapStateToProps, { getBalances, getFeesStatement })(
  Statement
);
