import React, { Component } from "react";
import TextInputGroup from "../../components/layout/TextInputGroup";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import { loginUser } from "../../action/authActions";
import { getAuthSessionId } from "../../services/Config";

class Login extends Component {
  componentDidMount() {
    const session_id = getAuthSessionId();
    if (session_id != null) {
      this.props.history.push("/");
    }
  }
  state = {
    regnum: "",
    password: "",
    errors: {},
  };

  onSubmit = (e) => {
    e.preventDefault();

    const { regnum, password } = this.state;

    // Check For Errors
    if (regnum === "") {
      this.setState({ errors: { regnum: "Registration Number is required" } });
      return;
    }

    if (password === "") {
      this.setState({ errors: { password: "Password is required" } });
      return;
    }

    this.props.loginUser(regnum, password);
  };

  onChange = (e) => this.setState({ [e.target.name]: e.target.value });

  render() {
    const { regnum, password, errors } = this.state;

    return (
      <div class="col-lg-6 mx-auto mt-5">
        <div className="card mb-3">
          <div className="card-header fw-bold">Login</div>
          <div className="card-body">
            <form>
              <TextInputGroup
                label="Registration Number"
                name="regnum"
                placeholder=""
                value={regnum}
                onChange={this.onChange}
                error={errors.regnum}
              />
              <TextInputGroup
                label="Password"
                name="password"
                type="password"
                placeholder=""
                value={password}
                onChange={this.onChange}
                error={errors.password}
              />
            </form>
          </div>
          <div class="card-footer">
            <div class="d-grid gap-2">
              <button
                onClick={this.onSubmit}
                className="btn btn-primary btn-lg btn-block mt-3"
              >
                Login
              </button>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default connect(null, { loginUser })(Login);
