import React, { Component } from "react";
import Programme from "./Programme";
import { connect } from "react-redux";
import { getStudentProgrammes } from "../../action/registrationActions";
import LoadingSpinner from "../LoadingSpinner";

class ProgrammeList extends Component {
  constructor(props) {
    super(props);
    this.state = {
      error: null,
      isLoaded: false,
      items: [],
    };
  }
  componentDidMount() {
    this.props.getStudentProgrammes();
  }
  render() {
    const { programmes } = this.props;
    console.log("PROGRAMMES");
    console.log(programmes);
    return (
      <div class="col-12 mb-3">
        <div class="card shadow-sm">
          <div class="card-header fw-bold">Programmes</div>
          <div class="card-body">
            {programmes != undefined ? (
              <ul class="list-group list-group-flush">
                {Object.entries(programmes).map((key, value) => {
                  return <Programme programme={programmes[value]} />;
                })}
              </ul>
            ) : (
              <LoadingSpinner class="text-center mx-auto" />
            )}
          </div>
          <div class="card-footer"></div>
        </div>
      </div>
    );
  }
}

const mapStateToProps = (state) => ({
  programmes: state.registration.programmes,
});

export default connect(mapStateToProps, { getStudentProgrammes })(
  ProgrammeList
);
