import React from 'react'
import {Bank, CurrencyExchange, PiggyBank, Bank2} from 'react-bootstrap-icons'
import {Link} from "react-router-dom";

export default function FinanceQuickLinks() {
    return (
        <div class="card">
            <div class="card-header fw-bold text-center">Financials</div>
            <div class="card-body">
                <div class="media">
                    <div class="media-body">
                        <Link to="/financial/statement/fees" class="list-group-item list-group-item-action">
                            <CurrencyExchange/> Fees Statement <span
                            class="badge bg-warning text-dark fw-bold"> RTGS</span></Link>
                        <Link to="financial/statement/fees/nostro"
                              class="list-group-item list-group-item-action"><CurrencyExchange/> Fees Statement <span
                            class="badge bg-warning text-dark fw-bold"> USD</span></Link>
                        <Link to="/financial/statement/rtgs"
                              class="list-group-item list-group-item-action"><Bank2/> RTGS
                            Payments</Link>
                        <Link to="/financial/statement/bank"
                              class="list-group-item list-group-item-action"><PiggyBank/> Bank
                            Deposits</Link>
                        <Link to="/financial/statement/overpayment" ng-if="!student_completed"
                              class="list-group-item list-group-item-action"><Bank/> Overpayment Funds Transfer</Link>
                    </div>
                </div>
            </div>
        </div>
    )
}
