import React, { Component } from "react";
import ProgrammeList from "../components/academic/ProgrammeList";
import Balance from "../components/finance/Balance";
import GoldCardBalance from "../components/finance/GoldCardBalance";
import RegistrationStatus from "../components/registration/RegistrationStatus";
import Header from "../components/layout/Header";
import FinanceQuickLinks from "../components/finance/FinanceQuickLinks";
import AcademicQuickLinks from "../components/academic/AcademicQuickLinks";

export default class Home extends Component {
  render() {
    return (
      <React.Fragment>
        <div class="row">
          <ProgrammeList class="mb-3" />
          <RegistrationStatus />
          <Balance />
          <GoldCardBalance />
        </div>
        <div class="row">
          <div class="col-lg-4">
            <FinanceQuickLinks />
          </div>

          <div class="col-lg-4">
            <AcademicQuickLinks />
          </div>
        </div>
      </React.Fragment>
    );
  }
}
