import React from "react";

export default function LoadingSpinner() {
  return (
    <div class="mx-auto text-center">
      <div
        class="spinner-border text-primary"
        style={{
          width: "3rem",
          height: "3rem",
          borderWidth: "1px",
        }}
        role="status"
      >
        <span class="sr-only">Loading...</span>
      </div>
    </div>
  );
}
